import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/christmastree',
    name: 'christmastree',
    component: () => import('@/views/HomeView.vue')
  },
  {
    path: '*',
    redirect: '/christmastree',
 }
]

const router = new VueRouter({
  routes
})

export default router
