import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  //类似组件的data
  state: {
    title: '组件和页面共享的数据',

    book: 'Vue',

    username: '小明',

    text: '2022-02-31',

    animal: ['白兔', '猴子', '鱼'],

    counts: [200, 46, 89, 96, 312],

    products: [
      {
        name: '电脑1',
        price: 1200
      },
     
      {
        name: '电脑2',
        price: 366
      },

      {
        name: '电脑3',
        price: 566
      },
    ]
  },

  //类似组件的methods
  mutations: {
    updateTitle(state, payload) {
      //payload: 载荷, 组件提交mutations携带的参数
      state.title = payload.data;
    },

    updateText(state, payload) {
      state.text = payload.data;
    },

    updateText1(state, payload) {
      state.text = payload.data;
    },

    updateText2(state, payload) {
      state.text = payload.data;
    },

    addAnimal(state, value) {
      state.animal.push(value);
    },

    //添加一个count
    addCount(state, value) {
      state.counts.push(value);
    }
  },

  //用于提交mutations, 类似组件的methods
  actions: {

    addOneAnimal({commit, dispatch}, value) {
      //context: vuex的上下文
      //value: 组件提交actions携带的参数

      // console.log('context ==> ', context);

      // context.commit('addAnimal', value);
      
      //提交mutations的addAnimal方法
      commit('addAnimal', value);

      //提交actions的testAction方法
      dispatch('testAction').then(res => {
        console.log('res ==> ', res);
        console.log('提交完毕testAction');
      }).catch(err => {
        console.log('err ==> ', err);
      })


      return '飞鸟';
    },

    testAction() {
      console.log('testAction');
      return '测试';
    },

    changeTitle({commit}, payload) {
      commit('updateTitle', payload);
    }

  },

  //过滤state数据, 类似组件的computed, 需要返回一个结果
  //只读的, 如果使得getters变化, 修改更改其依赖的state数据
  getters: {
    filterCount(state) {
      return state.counts.filter(v => v >= 90);
    },

    product500(state) {
      return state.products.filter(item => item.price >= 500);
    }
  }
  
})
